import {
  ComponentProps,
  withStreamlitConnection,
  StreamlitComponentBase,
  Streamlit,
} from 'streamlit-component-lib'
import React, { 
  ReactNode } from 'react'

interface State {
  hostName?: string
}

class SpecialHost extends StreamlitComponentBase<State> {

  public constructor(props: ComponentProps) {
    super(props)
    this.state = {};

    this._update()
  }

  public render = (): ReactNode => {
    return (
      <></>
    )
  }

  private _update = () : void => {
    let h = localStorage.getItem("__host__");
    this.setState({ hostName: h } as State);
    Streamlit.setComponentValue(h);
  }
}

export default withStreamlitConnection(SpecialHost)
